<template>
    <div class="box">
        <div class="header">
            <img :src="imgs" alt="" >
        </div>
        <div class="bbox">
            <div class="left">
                 <img :src="imgs" alt="" >
            </div>
            <div class="right">
                 <h3>{{$route.query.ids}}</h3>
                 <p>作者：{{author}}</p>
                <p>更新至：{{now}}</p>
                <p>
                    类型：
                    <span  class="type" v-for="(item,i) in type" :key="i">{{item.name}}</span>
                </p>
            </div>
        </div>
        <div class="list">
             <span>{{jianjie}}</span>
            <ul class="mulu">
                <li v-for="(item,i) in mulu" @click="chapter(i)" :key="i" :data="item">
                    <span>{{item.chapter_name}}</span>
                </li>
            </ul>
        </div> 
        <div class="footer">
            <p @click="collection()">收藏</p>
            <router-link :to="{path:'/read',query:{'postdata':postdata}}" tag="p">{{msg?"开始":"继续"}}</router-link>
        </div>
        <div class="bottom" ></div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            tit:"",
            inde:0,
            author:"",
            jianjie:"",
            mulu:[],
            type:[],
            now:"",
            id:"",
            imgs:"",
            msg:true,
            postdata:{}
        }
    },
    methods:{
        chapter(index){
            var that = this;
            that.postdata={
                num:that.mulu[index].end_num,
                urls:that.mulu[index].rule,
                id:that.id,
                hua:index,
                inde:0,
                max:that.mulu.length
            }
            that.postdata = JSON.stringify(that.postdata)
            var lis = document.getElementsByClassName("mulu")[0].getElementsByTagName("li")
            for(var i=0;i<lis.length;i++){
                lis[i].className=""
            }
            lis[index].className='now'
        },
        collection(){
            var that = this
            if(that.msg){
                var arr = localStorage.collection;
                if(typeof(arr)=="undefined"&& arr.length>2){
                    arr=[]
                }else{
                    arr = JSON.parse(arr)
                }
                var obj={
                    tit:that.$route.query.ids,
                    author:that.author,
                    imgs:that.imgs,
                    id:that.id,
                    hua:0,
                    num:that.mulu[0].end_num,
                    urls:that.mulu[0].rule,
                    inde:0,
                    max:that.mulu.length
                }
                arr.push(obj)
                arr= JSON.stringify(arr)
                localStorage.setItem("collection",arr)
                alert("成功收藏")
            }else{
                alert("已经收藏了")
            }
        }
    },
   created(){
       var that = this
       var ids = that.$route.query.ids;
       that.tit=ids
       var url = "https://www.manhuatai.com/api/getsortlist/?search_key="+ids;
       this.axios.get(url).then(
             function(res){
                    var ids = res.data.data[0].comic_id
                    that.id = ids;
                    var urls = "https://www.manhuatai.com/api/getComicInfoBody?comic_id="+ids
                    that.axios.get(urls).then(
                        function(res){
                            var shuju = res.data.data;
                            that.imgs=shuju.cover_list[0]
                            that.author=shuju.comic_author
                            that.mulu=shuju.comic_chapter;
                            that.mulu=that.mulu.reverse();
                            that.jianjie = shuju.comic_desc
                            that.type = shuju.comic_type_new.slice(0,3)
                            that.now = shuju.last_chapter_name
                            that.postdata={
                                num:that.mulu[0].end_num,
                                urls:that.mulu[0].rule,
                                id:that.id,
                                hua:0,
                                inde:0,
                                max:that.mulu.length
                            }
                             /* 判断是否收藏 */
                            var colle = localStorage.collection;
                            if(typeof(colle)!="undefined" && colle.length>2){
                                colle = JSON.parse(colle)
                                for (var i = 0;i<colle.length;i++){
                                    if(colle[i].id==that.id){
                                        that.msg=false
                                        that.postdata.inde=colle[i].inde
                                    }
                                }
                            }
                            
                        }
                    )
                }
        )
   },
   mounted(){
       const mui=window.mui
         /* 返回键 */
        mui.init({
            swipeBack: false, //关闭右滑关闭功能(默认就是false)
            keyEventBind: {
                backbutton: true //开启back按键监听( 默认就是true)
            }
        });
        // //首页返回键处理
        // //处理逻辑：1秒内，连续两次按返回键，则退出应用；
        var first = null;
      mui.back = function() {
       //首次按键，提示 再按一次退出应用
       if (!first) {
            first = new Date().getTime(); //记录第一次按下回退键的时间
            mui.toast("再按一次退出应用"); //给出提示
            history.go(-1); //回退到上一页面
           setTimeout(function() {
              //1s中后清除
            first = null;
             }, 1000);
           } else {
             if (new Date().getTime() - first < 1000) {
             //如果两次按下的时间小于1s，
               window.plus.runtime.quit(); //那么就退出app
             }
           }
       }
   },
   beforeDestroy(){
       var that = this;
        var obj={
                tit:that.tit,
                author:that.author,
                imgs:that.imgs,
                id:that.id,
                inde:0,
                max:that.mulu.length
            }
        obj = JSON.stringify(obj)
        sessionStorage.current=obj
   }
}
</script>
<style scoped>
.header{
    width: 100%;
    height:8rem;
    overflow: hidden;
    opacity: 0.5;
}
.header img{
    width: 100%;
}
    .box{
        width: 100%;
        background-color: #b4b4b4;
    }
    .bbox{
        width: 100%;
        height:9rem;
        display: flex;
    }
    .left{
        flex: 1;
         border: 1px solid black;
         text-align: center;
         padding:2px;
    }
    .left img{
        width: 90%;
    }
    .right{
        flex: 2;
         border: 1px solid green;
    }
    .right p{
        text-align: left;
        margin-top: 0.5rem;
    }
    .right .type{
        display: inline-block;
        width: 3rem;
        height:2rem;
        text-align: center;
        line-height:2rem;
        border-radius:10px;
        background-color:#cccccc;
        margin-left: 2px;
    }
    .mulu{
        width: 100%;
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        display: flex;
        flex-wrap: wrap;
        justify-content:space-around;
    }
    .mulu li{
        width: 4rem;
        height:2rem;
        text-align: center;
        line-height:2rem;
        border: 1px solid #cccccc;
        overflow: hidden;
        border-radius: 15%;
        margin-left:5px;
        margin-right: 5px;
        margin-bottom: 5px;
    }
.mulu .now{
    background-color:orange;
}
.footer{
    position: fixed;
    width: 100%;
    height:4rem;
    padding: 1rem 0;
    text-align: center;
    background-color:#cccccc;
    bottom: 0;
    display: flex;
    justify-content:space-around;
}
.footer p{
    width:4rem;
    height: 2rem;
    line-height: 2rem;
    background-color:#ececec;
    border-radius:10px;
}
.bottom{
    width: 100%;
    height: 4rem;
}
</style>